import React from "react"
import AppLink from "./AppLink"
import './AppLinks.css'

const AppLinks = (props) => (
    <div className= "AppLinks">
        <div className="AppLinksGroup">
            {props.links.map((appLink,i) => <AppLink key={appLink.id} link={appLink.url} title={appLink.source}/>)}
        </div>
    </div>
)

export default AppLinks