import React from 'react'
import PropTypes from "prop-types"
import './Card.css'
import { render } from 'react-dom'
import Icons from './icons'

class Card extends React.Component {
    constructor(props) {
        super(props)
        this.state = { icon: Icons[props.iconIndex]}
    }
     render() {
         return (
        <div className = "Card">
        <img src={ Icons[this.props.iconIndex]} alt=""/>
        <h3>{this.props.title}</h3>
        <p>{this.props.text}</p>
    </div>
         )
     }
}


export default Card