import React from "react"

class TechList extends React.Component {

    constructor(props) {
        super(props)
        this.filterListEvent = props.click
        this.state = {appsData : props.data.apps, selected: "ALL"}
    }

    updateSelected = (name) => {
        this.filterListEvent(name)
        this.setState ({selected: name})

    }
render() {
    return (
        <ul className="portfolio-filter">
        <li className= {this.state.selected == 'ALL' ? 'portfolio-active' : ''}  onClick= {() => this.updateSelected('ALL')}>All</li>
        <li className= {this.state.selected == 'UNITY' ? 'portfolio-active' : ''} onClick= {() => this.updateSelected('UNITY')}>Unity</li>
        <li className= {this.state.selected == 'IOS' ? 'portfolio-active' : ''} onClick= {() => this.updateSelected('IOS')}>iOS</li>
        <li className= {this.state.selected == 'ANDROID' ? 'portfolio-active' : ''} onClick= {() => this.updateSelected('ANDROID')}>Android</li>
        <li className= {this.state.selected == 'OTHER' ? 'portfolio-active' : ''} onClick= {() => this.updateSelected('OTHER')}>Others</li>
        <li className= {this.state.selected == 'OPEN' ? 'portfolio-active' : ''} onClick= {() => this.updateSelected('OPEN')}>Open Source</li>
    </ul>
    )
}
}

export default TechList

