import React from 'react'
import AppInfo from './AppInfo'
import Card from './Card'
import './AppCard.css'

const AppCard = (props) => (
    <div className="AppCard">
    <Card title = {props.title} iconIndex = {props.iconIndex}/>
    <AppInfo description = {props.description} info = {props.info} links = {props.links}/>
    </div>

)

export default AppCard